import { createContext, useContext, ReactNode } from 'react';

// Define the client interface based on your LangGraph client
interface LangGraphClient {
  process: (message: string) => Promise<any>;
}

interface ClientContextType {
  client: LangGraphClient | null;
}

const ClientContext = createContext<ClientContextType>({ client: null });

interface ClientProviderProps {
  children: ReactNode;
  client: LangGraphClient;
}

export function ClientProvider({ children, client }: ClientProviderProps) {
  return (
    <ClientContext.Provider value={{ client }}>
      {children}
    </ClientContext.Provider>
  );
}

export function useClient() {
  const context = useContext(ClientContext);
  if (!context) {
    throw new Error('useClient must be used within a ClientProvider');
  }
  return context.client;
}
